.slider {
  &__wrap {
    --arrows-gap: -30px;
    position: relative; }
  &__prev,
  &__next {
    position: absolute;
    top: 50%;
    background-color: transparent; }
  &__prev {
    @include icon-arrow(15, 15, 1, $c-text, 45);
    display: inline-block;
    transform: translate(0, -50%) rotate (45deg);
    left: var(--arrows-gap); }
  &__next {
    @include icon-arrow(15, 15, 1, $c-text, -135);
    display: inline-block;
    transform: translate(0, -50%) rotate (45deg);
    right: var(--arrows-gap); }

  &--similar {
    .slick-track {
      @include flex(); }
    .slick-slide {
      @include flex(flex-start, normal, column);
      height: auto;
      > div {
        height: 100%; }
      .slide {
        height: 100%;
        display: flex !important; } } } }
